<template>
  <div class="dashboard">
    <!-- 顶部控制栏 -->
    <div class="controls">
      <el-select v-model="selectedRegion" placeholder="区域" style="width: 180px;" @change="fetchInstances">
        <el-option v-for="item in regionOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
      </el-select>

      <el-select v-model="selectedInstance" placeholder="实例" style="width: 180px;" @change="fetchVnics"
                 :disabled="!selectedRegion">
        <el-option v-for="item in instanceOptions" :key="item.value" :label="item.label"
                   :value="item.value"></el-option>
      </el-select>

      <el-select v-model="selectedVnic" placeholder="vnic" style="width: 180px;" :disabled="!selectedInstance">
        <el-option v-for="item in vnicOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
      </el-select>

      <el-select v-model="timeInterval" placeholder="时间间隔" style="width: 120px;">
        <el-option v-for="item in timeOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
      </el-select>

      <el-date-picker v-model="QueryParams.beginTime" type="datetime" placeholder="开始时间"
                      style="width: 180px;"></el-date-picker>
      <el-date-picker v-model="QueryParams.endTime" type="datetime" placeholder="结束时间"
                      style="width: 180px;"></el-date-picker>
      <el-button type="primary" @click="fetchData()" :loading="loading">查询</el-button>
    </div>

    <!-- 主内容区域 -->
    <div class="main-content">
      <!-- 信息面板 -->
      <el-card class="info-panel" shadow="hover">
        <el-descriptions
            title="配置详情"
            border
            column="1"
            size="small"
        >
          <el-descriptions-item label="当前配置">{{ currentConfig }}</el-descriptions-item>
          <el-descriptions-item label="区域">{{ selectedRegion }}</el-descriptions-item>
          <el-descriptions-item label="实例数量">{{ instanceCount }}</el-descriptions-item>
          <el-descriptions-item label="本月入站流量总计">{{ inboundTraffic }}</el-descriptions-item>
          <el-descriptions-item label="本月出站流量总计">{{ outboundTraffic }}</el-descriptions-item>
        </el-descriptions>
      </el-card>

      <!-- 数据展示 -->
      <div class="chart-container">
        <div ref="chartRef" style="width: 100%; height: 400px;"></div>
      </div>
    </div>
  </div>
</template>


<script>
import * as echarts from "echarts";
import {ElMessage} from "element-plus";

export default {
  props: {
    ociCfgId: String,
    username: String,
  },
  mounted() {
    this.currentConfig = this.username
    this.updateChart(this.data)
    this.getCondition()
  },
  data() {
    return {
      loading: true,
      selectedRegion: "",
      selectedInstance: "",
      selectedVnic: "",
      timeInterval: "",
      startTime: null,
      endTime: null,
      QueryParams: {
        ociCfgId: this.ociCfgId,
        beginTime: null,
        endTime: null,
        region: '',
        inQuery: '',
        outQuery: '',
        namespace: "oci_vcn"
      },
      regionOptions: [],
      instanceOptions: [
        {value: "instance-1", label: "实例1"},
        {value: "instance-1", label: "实例2"},
      ],
      vnicOptions: [
        {value: "vnic1", label: "vnic1"},
        {value: "vnic2", label: "vnic2"},
      ],
      timeOptions: [
        {value: "[1m]", label: "分钟"},
        {value: "[60m]", label: "小时"},
        {value: "[1440m]", label: "天"},
      ],
      currentConfig: "首尔",
      instanceCount: 0,
      inboundTraffic: "0 MB",
      outboundTraffic: "0 MB",
      data: {
        time: [],
        inbound: [],
        outbound: [],
      }

    };
  },
  methods: {
    async getCondition() {
      await this.$axios.post("/traffic/getCondition", new URLSearchParams({ociCfgId: this.ociCfgId}))
          .then((response) => {
            if (response.data.success) {
              const resData = response.data.data
              this.regionOptions = resData.regionOptions
              this.selectedRegion = resData.regionOptions[0].value
              this.instanceOptions = resData.instanceOptions[this.selectedRegion] || [];
              this.fetchInstances()
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
          })
          .catch((error) => {
            ElMessage({
              message: "数据获取失败:" + error,
              type: 'error',
              duration: 2000 // 设置提示显示时间，单位是毫秒
            });
          });
    },
    async fetchData() {
      this.loading = true
      await this.fetchInstances()

      this.QueryParams.inQuery = "VnicToNetworkBytes" + this.timeInterval + "{resourceId = \"" + this.selectedVnic + "\"}.sum()"
      this.QueryParams.outQuery = "VnicFromNetworkBytes" + this.timeInterval + "{resourceId = \"" + this.selectedVnic + "\"}.sum()"
      this.QueryParams.region = this.selectedRegion
      await this.$axios.post("/traffic/data", {...this.QueryParams})
          .then((response) => {
            if (response.data.success) {
              this.data = response.data.data
              this.updateChart(this.data);
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
          })
          .catch((error) => {
            ElMessage({
              message: "数据获取失败:" + error,
              type: 'error',
              duration: 2000 // 设置提示显示时间，单位是毫秒
            });
          })
          .finally(() => {
            this.loading = false
          })
    },
    // 根据选中的区域获取实例
    async fetchInstances() {
      this.loading = true

      if (!this.selectedRegion) return;

      await this.$axios.post("/traffic/fetchInstances", new URLSearchParams({
        ociCfgId: this.ociCfgId,
        region: this.selectedRegion
      })).then((response) => {
        if (response.data.success) {
          const resData = response.data.data
          this.instanceCount = resData.instanceCount
          this.inboundTraffic = resData.inboundTraffic
          this.outboundTraffic = resData.outboundTraffic
        } else {
          ElMessage({
            message: "获取区域实例失败",
            type: 'error',
            duration: 2000 // 设置提示显示时间，单位是毫秒
          });
        }
      }).catch((error) => {
        ElMessage({
          message: "获取区域实例失败:" + error,
          type: 'error',
          duration: 2000 // 设置提示显示时间，单位是毫秒
        });
      });
      this.loading = false
    },

    // 根据选中的实例获取 VNIC
    async fetchVnics() {
      this.loading = true
      this.selectedVnic = ""; // 清空 VNIC 选择
      this.vnicOptions = [];

      if (!this.selectedInstance) return;

      await this.$axios.post("/traffic/fetchVnics", new URLSearchParams({
        ociCfgId: this.ociCfgId,
        region: this.selectedRegion,
        instanceId: this.selectedInstance
      })).then((response) => {
        if (response.data.success) {
          this.vnicOptions = response.data.data
        } else {
          ElMessage({
            message: "获取区域实例vnic失败:",
            type: 'error',
            duration: 2000 // 设置提示显示时间，单位是毫秒
          });
        }
      }).catch((error) => {
        ElMessage({
          message: "获取区域实例vnic失败:" + error,
          type: 'error',
          duration: 2000 // 设置提示显示时间，单位是毫秒
        });
      });
      this.loading = false
    },
    updateChart(data) {
      const chartInstance = echarts.init(this.$refs.chartRef);
      const option = {
        tooltip: {
          trigger: "axis",
        },
        legend: {
          orient: "horizontal", // 设置图例为水平排列
          bottom: 10,           // 图例距离底部的距离
          align: "auto",        // 自动对齐
          data: ["入站流量", "出站流量"],
        },
        xAxis: {
          type: "category",
          data: data.time,
          axisLabel: {
            rotate: 15 // X轴标签旋转45度，避免重叠
          }
        },
        yAxis: {
          type: "value",
          axisLabel: {
            formatter: "{value} MB" // 在Y轴数值后加上 MB
          }
        },
        series: [
          {
            name: "入站流量",
            type: "bar",
            data: data.inbound,
            itemStyle: {color: "#5470c6"},
          },
          {
            name: "出站流量",
            type: "bar",
            data: data.outbound,
            itemStyle: {color: "#91cc75"},
          },
        ],
      };
      chartInstance.setOption(option);
    },
  },
};
</script>

<style scoped>
.dashboard {
  padding: 20px;
}

.controls {
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
}

.main-content {
  display: flex; /* 使用 Flexbox 布局 */
}

.info-panel {
  height: auto; /* 自动高度 */
  max-width: 400px;
  margin: 20px auto;
  //background-color: #ffffff;
  border-radius: 20px;
  padding: 10px;
}

.chart-container {
  flex-grow: 1; /* 图表容器占据剩余空间 */
  margin-left: 20px; /* 添加间距 */
}
</style>

