<template>
  <el-button type="primary" @click="inboundDialogVisible = true">添加入站规则</el-button>
  <el-dialog v-model="inboundDialogVisible" title="添加入站规则" width="50%">
    <el-form label-width="120px">
      <el-form-item label="无状态">
        <el-switch v-model="inboundRule.isStateless" active-text="启用" inactive-text="禁用"/>
      </el-form-item>
      <el-form-item label="源类型">
        <el-select v-model="inboundRule.sourceType" placeholder="请选择">
          <el-option label="CIDR" value="CIDR_BLOCK"/>
          <el-option label="服务" value="SERVICE_CIDR_BLOCK"/>
        </el-select>
      </el-form-item>
      <el-form-item label="源 CIDR">
        <el-input v-model="inboundRule.source" placeholder="请输入 CIDR，如 10.0.0.0/24"/>
      </el-form-item>
      <el-form-item label="IP 协议">
        <el-select v-model="inboundRule.protocol" placeholder="请选择">
          <el-option v-for="item in protocols" :key="item.value" :label="item.label" :value="item.value"/>
        </el-select>
      </el-form-item>
      <el-form-item label="类型" v-if="inboundRule.protocol === '1'">
        <el-input v-model="inboundRule.icmpOptions.type" placeholder="全部"/>
      </el-form-item>
      <el-form-item label="代码" v-if="inboundRule.protocol === '1'">
        <el-input v-model="inboundRule.icmpOptions.code" placeholder="全部"/>
      </el-form-item>
      <el-form-item label="源端口范围"
                    v-if="['6', '17'].includes(inboundRule.protocol)"
      >
        <el-input v-model="inboundRule.sourcePort" placeholder="示例: 80、20-22"/>
      </el-form-item>
      <el-form-item label="目标端口范围"
                    v-if="['6', '17'].includes(inboundRule.protocol)"
      >
        <el-input v-model="inboundRule.destinationPort" placeholder="示例: 80、20-22"/>
      </el-form-item>
      <el-form-item label="说明">
        <el-input v-model="inboundRule.description" type="textarea" maxlength="255" show-word-limit
                  placeholder="最多 255 个字符"/>
      </el-form-item>
    </el-form>
    <template #footer>
      <el-button @click="inboundDialogVisible = false">取消</el-button>
      <el-button type="primary" @click="addInboundRule" :loading="addIngressLoading">添加入站规则</el-button>
    </template>
  </el-dialog>

  <el-button type="primary" @click="outboundDialogVisible = true">添加出站规则</el-button>
  <el-dialog v-model="outboundDialogVisible" title="添加出站规则" width="50%">
    <el-form label-width="120px">
      <el-form-item label="无状态">
        <el-switch v-model="outboundRule.isStateless" active-text="启用" inactive-text="禁用"/>
      </el-form-item>
      <el-form-item label="目的地类型">
        <el-select v-model="outboundRule.destinationType" placeholder="请选择">
          <el-option label="CIDR" value="CIDR_BLOCK"/>
          <el-option label="服务" value="SERVICE_CIDR_BLOCK"/>
        </el-select>
      </el-form-item>
      <el-form-item label="目的地 CIDR">
        <el-input v-model="outboundRule.destination" placeholder="请输入 CIDR，如 10.0.0.0/24"/>
      </el-form-item>
      <el-form-item label="IP 协议">
        <el-select v-model="outboundRule.protocol" placeholder="请选择">
          <el-option v-for="item in protocols" :key="item.value" :label="item.label" :value="item.value"/>
        </el-select>
      </el-form-item>
      <el-form-item label="类型" v-if="outboundRule.protocol === '1'">
        <el-input v-model="outboundRule.icmpOptions.type" placeholder="全部"/>
      </el-form-item>
      <el-form-item label="代码" v-if="outboundRule.protocol === '1'">
        <el-input v-model="outboundRule.icmpOptions.code" placeholder="全部"/>
      </el-form-item>
      <el-form-item label="源端口范围"
                    v-if="['6', '17'].includes(outboundRule.protocol)"
      >
        <el-input v-model="outboundRule.sourcePort" placeholder="示例: 80、20-22"/>
      </el-form-item>
      <el-form-item label="目标端口范围"
                    v-if="['6', '17'].includes(outboundRule.protocol)"
      >
        <el-input v-model="outboundRule.destinationPort" placeholder="示例: 80、20-22"/>
      </el-form-item>
      <el-form-item label="说明">
        <el-input v-model="outboundRule.description" type="textarea" maxlength="255" show-word-limit
                  placeholder="最多 255 个字符"/>
      </el-form-item>
    </el-form>
    <template #footer>
      <el-button @click="outboundDialogVisible = false">取消</el-button>
      <el-button type="primary" @click="addOutboundRule" :loading="addEgressLoading">添加出站规则</el-button>
    </template>
  </el-dialog>

<!--  <el-button @click="releaseSecurityRule" type="success">放行所有出/入站端口</el-button>-->

  <el-divider class="divider"/>

  <div v-if="!showSecurityRuleList">
    <el-button type="success"
               style="margin-bottom: 20px;"
               @click="reloadSecurityRuleList">
      刷新
    </el-button>
    <el-input
        v-model="GetSecurityRuleListPageParams.keyword"
        placeholder="模糊搜索"
        @input="getSecurityRuleList"
        clearable prefix-icon="Search"
        style="margin-left: 20px; width: 200px; margin-bottom: 20px;"
    />
    <el-select
        v-model="GetSecurityRuleListPageParams.type"
        @change="getSecurityRuleList"
        placeholder="开机任务状态筛选"
        style="margin-left: 20px; width: 180px; margin-bottom: 20px;"
    >
      <el-option label="入站规则" :value="0"></el-option>
      <el-option label="出站规则" :value="1"></el-option>
    </el-select>
    <el-button
        type="danger"
        @click="batchRemove"
        :disabled="!batchSelected.length"
        style="margin-left: 20px; margin-bottom: 20px;"
    >
      批量删除
    </el-button>
    <!-- 可滑动的表格 -->
    <div style="overflow-x: auto;">
      <el-table
          :data="ruleList"
          style="width: 100%; min-width: 800px;"
          v-loading="loading"
          @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="35"/>
        <el-table-column fixed prop="isStateless" label="无状态"></el-table-column>
        <el-table-column label="源">
          <template #default="{ row }">
            <el-tag type="primary">
              {{ row.sourceOrDestination }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="protocol" label="协议"></el-table-column>
        <el-table-column prop="sourcePort" label="源端口范围"></el-table-column>
        <el-table-column prop="destinationPort" label="目的地端口范围"></el-table-column>
        <el-table-column prop="typeAndCode" label="类型和代码"></el-table-column>
        <el-table-column prop="description" label="说明"/>

        <el-table-column fixed="right" label="操作" width="150">
          <template #default="{ row }">
            <el-button @click="handleRemove(row)" type="danger" link>
              删除
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!-- 分页控制 -->
    <el-pagination
        @current-change="handlePageChange"
        @size-change="handleSizeChange"
        v-model:current-page="GetSecurityRuleListPageParams.currentPage"
        v-model:page-size="GetSecurityRuleListPageParams.pageSize"
        :page-sizes="[5, 10, 20, 50]"
        :total="total"
        layout="total, sizes, prev, pager, next, jumper"
        style="margin-top: 20px;"
    />
  </div>
</template>

