  methods: {
    async reloadSecurityRuleList() {
      this.GetSecurityRuleListPageParams.cleanReLaunch = true
      this.loading = true
      this.getSecurityRuleList()
      this.GetSecurityRuleListPageParams.cleanReLaunch = false
    },
    async getSecurityRuleList() {
      this.loading = true
      this.GetSecurityRuleListPageParams.ociCfgId = this.ociCfgId
      this.GetSecurityRuleListPageParams.vcnId = this.vcnId
      await this.$axios.post('/securityRule/page', {...this.GetSecurityRuleListPageParams})
          .then(response => {
            if (response.data.success) {
              this.ruleList = response.data.data.records;
              this.total = response.data.data.total;
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
            this.loading = false
          })
          .catch(error => {
            console.error('Error:', error)
          })
          .finally(() => {
            this.batchSelected = []
          })
    },
    handleRemove(row) {
      this.batchSelected.push(row)
      this.batchRemove()
    },
    async batchRemove() {
      const confirmed = window.confirm('确定要删除安全规则吗');
      if (!confirmed) {
        this.$message.info('已取消');
        return
      }

      this.loading = true
      this.RemoveSecurityRuleParams.ociCfgId = this.ociCfgId
      this.RemoveSecurityRuleParams.vcnId = this.vcnId
      this.RemoveSecurityRuleParams.type = this.GetSecurityRuleListPageParams.type
      this.RemoveSecurityRuleParams.ruleIds = this.batchSelected.map(record => record.id);
      await this.$axios.post('/securityRule/remove', {...this.RemoveSecurityRuleParams})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: '删除规则成功',
                type: 'success',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
              this.reloadSecurityRuleList()
              this.batchSelected = []
              this.RemoveSecurityRuleParams.ruleIds = []
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
    },
    async addInboundRule() {
      this.addIngressLoading = true
      await this.$axios.post('/securityRule/addIngress', {
        ociCfgId: this.ociCfgId,
        vcnId: this.vcnId,
        inboundRule: this.inboundRule
      })
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: '添加入站规则成功',
                type: 'success',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
              this.inboundDialogVisible = false
              this.reloadSecurityRuleList()
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
