<template>
  <div>
    <span style="font-size: 16px; font-weight: bold;">当前配置：{{ this.username }}</span>
    <span style="font-size: 16px; font-weight: bold; margin-left: 20px" v-if="showSecurityRuleList">当前VCN：{{ this.vcnName }}</span>
    <el-divider class="divider"/>
    <div v-if="!showSecurityRuleList">
      <el-button type="success"
                 style="margin-bottom: 20px;"
                 @click="reloadVcnList">
        刷新
      </el-button>
      <el-input
          v-model="pageParams.keyword"
          placeholder="模糊搜索"
          @input="getVcnList"
          clearable prefix-icon="Search"
          style="margin-left: 20px; width: 200px; margin-bottom: 20px;"
      />
      <el-button
          type="danger"
          @click="batchRemove"
          :disabled="!batchSelected.length"
          style="margin-left: 20px; margin-bottom: 20px;"
      >
        批量删除
      </el-button>
      <!-- 可滑动的表格 -->
      <div style="overflow-x: auto;">
        <el-table
            :data="vcnPageRsp"
            style="width: 100%; min-width: 800px;"
            v-loading="loading"
            @selection-change="handleSelectionChange"
        >
          <el-table-column type="selection" width="35"/>
          <el-table-column fixed label="名称" width="300">
            <template #default="{ row }">
              <el-tooltip :content="row.displayName" placement="top">
                {{ row.displayName }}
              </el-tooltip>
            </template>
          </el-table-column>
          <el-table-column label="状态">
            <template #default="{ row }">
              <el-tag :type="row.status === 'AVAILABLE' ? 'success' : 'info'">
                {{ row.status }}
              </el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="createTime" label="创建时间"/>

          <el-table-column fixed="right" label="操作" width="150">
            <template #default="{ row }">
              <el-button @click="handleSecurityRuleList(row)" type="primary" link>
                安全列表
              </el-button>
              <el-button @click="handleRemove(row)" type="danger" link>
                删除
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>

      <!-- 分页控制 -->
      <el-pagination
          @current-change="handlePageChange"
          @size-change="handleSizeChange"
          v-model:current-page="pageParams.currentPage"
          v-model:page-size="pageParams.pageSize"
          :page-sizes="[5, 10, 20, 50]"
          :total="total"
          layout="total, sizes, prev, pager, next, jumper"
          style="margin-top: 20px;"
      />
    </div>

    <div v-else>
      <el-button @click="showSecurityRuleList = false">返回VCN列表</el-button>
      <SecurityRuleList :ociCfgId="ociCfgId" :username="username" :vcnId="vcnId" :vcnName="vcnName"/>
    </div>
  </div>

</template>

<script>
import {ElMessage} from "element-plus";
import SecurityRuleList from "@/components/vcn/SecurityRuleList.vue";


export default {
  components: {SecurityRuleList},
  mounted() {
    this.ociCfgId = this.$route.query.cfgId
    this.pageParams.ociCfgId = this.$route.query.cfgId
    this.RemoveVcnParams.ociCfgId = this.$route.query.cfgId
    this.username = this.$route.query.username
    this.getVcnList()
  },
  data() {
    return {
      showSecurityRuleList: false, // 控制是否显示 SecurityRuleList
      vcnId: null, // 记录当前选中的 VCN ID
      vcnName: '',
      username: '',
      ociCfgId: '',
      loading: true,
      pageParams: {
        cleanReLaunch: false,
        ociCfgId: '',
        keyword: '',
        currentPage: 1,
        pageSize: 10,
      },
      vcnPageRsp: [
        {
          id: '1',
          displayName: '123',
          status: 'AVAILABLE',
          createTime: '2025-03-03 22:19:18'
        }
      ],
      total: 0,
      batchSelected: [],
      RemoveVcnParams: {
        ociCfgId: '',
        vcnIds: []
      }

    };
  },
  methods: {
    reloadVcnList() {
      this.pageParams.cleanReLaunch = true
      this.loading = true
      this.getVcnList()
      this.pageParams.cleanReLaunch = false
    },
    async getVcnList() {
      await this.$axios.post('/vcn/page', {...this.pageParams})
          .then(response => {
            if (response.data.success) {
              this.vcnPageRsp = response.data.data.records;
              this.total = response.data.data.total;
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
            this.loading = false
          })
          .catch(error => {
            console.error('Error:', error)
          })
    },
    handleRemove(row) {
      this.batchSelected.push(row)
      this.batchRemove()
    },
    async batchRemove() {
      const confirmed = window.confirm('确定要删除VCN吗');
      if (!confirmed) {
        this.$message.info('已取消');
        return
      }

      this.RemoveVcnParams.vcnIds = this.batchSelected.map(record => record.id);
      await this.$axios.post('/vcn/remove', {...this.RemoveVcnParams})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
              this.reloadVcnList()
              this.batchSelected = []
              this.RemoveVcnParams.vcnIds = []
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
    },
    handleSecurityRuleList(row) {
      this.vcnId = row.id; // 记录选中的 VCN ID
      this.vcnName = row.displayName; // 记录选中的 VCN ID
      this.showSecurityRuleList = true; // 切换到 SecurityRuleList
    },
    handlePageChange(page) {
      this.pageParams.currentPage = page;
      this.getVcnList();
    },
    handleSizeChange(size) {
      this.pageParams.currentPage = 1;
      this.pageParams.pageSize = size;
      this.getVcnList();
    },
    handleSelectionChange(val) {
      this.batchSelected = val;
    },
  }
}
</script>

<style scoped>

