<template>
  <div class="user-page" v-loading="loading">
    <el-card class="user-info-card">
      <div class="clearfix">
        <span>租户信息</span>
      </div>
      <el-descriptions :column="1" border>
        <el-descriptions-item label="当前配置（点击修改）">
          <el-button @click="handleUpdateUsername" type="success" link style="margin-left: 0">{{ username }}</el-button>
        </el-descriptions-item>
        <el-descriptions-item label="租户名称">{{ tenantInfo.name }}</el-descriptions-item>
        <el-descriptions-item label="homeRegionKey">{{ tenantInfo.homeRegionKey }}</el-descriptions-item>
        <el-descriptions-item label="upiIdcsCompatibilityLayerEndpoint">{{
            tenantInfo.upiIdcsCompatibilityLayerEndpoint
          }}
        </el-descriptions-item>
        <el-descriptions-item label="租户ID">{{ tenantInfo.id }}</el-descriptions-item>
        <el-descriptions-item label="描述">{{ tenantInfo.description }}</el-descriptions-item>
        <el-descriptions-item label="已订阅的区域">
          <el-tag v-for="region in tenantInfo.regions" :key="region" style="margin-right: 10px">{{ region }}</el-tag>
        </el-descriptions-item>
      </el-descriptions>
    </el-card>

    <el-card class="user-list-card">
      <div class="clearfix">
        <span>用户列表</span>
      </div>
      <div style="overflow-x: auto;">
        <el-table
            :data="tenantInfo.userList"
            style="width: 100%; min-width: 800px;"
        >
          <el-table-column type="selection" width="35"/>
          <el-table-column fixed label="名称">
            <template #default="{ row }">
              <el-tooltip :content="row.name" placement="top">
                {{ row.name }}
              </el-tooltip>
            </template>
          </el-table-column>
          <el-table-column label="邮箱">
            <template #default="{ row }">
              <el-tooltip :content="row.email" placement="top">
                <el-tag type="primary">
                  {{ row.email }}
                </el-tag>
              </el-tooltip>
            </template>
          </el-table-column>
          <el-table-column label="状态">
            <template #default="{ row }">
              <el-tag :type="row.lifecycleState === 'ACTIVE' ?  'success' : 'info' ">
                {{ row.lifecycleState }}
              </el-tag>
            </template>
          </el-table-column>
          <el-table-column label="邮箱验证状态">
            <template #default="{ row }">
              <el-tag :type="row.emailVerified ?  'success' : 'danger' ">
                {{ row.emailVerified ? '已验证' : '未验证' }}
              </el-tag>
            </template>
          </el-table-column>
          <el-table-column label="是否绑定MFA">
            <template #default="{ row }">
              <el-tag :type="row.isMfaActivated ?  'success' : 'info' ">
                {{ row.isMfaActivated ? '已绑定' : '未绑定' }}
              </el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="timeCreated" label="创建时间"></el-table-column>
          <el-table-column prop="lastSuccessfulLoginTime" label="最后成功登录时间"></el-table-column>

          <el-table-column fixed="right" label="操作" width="150">
            <template #default="{ row }">
              <el-button @click="handleEdit(row)" type="primary" link>
                编辑
              </el-button>
              <el-button @click="deleteUser(row)" type="danger" link>
                删除
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </el-card>
  </div>


  <el-dialog :title="'编辑用户：'+UpdateUserName+ ' （🔔注意：部分功能可能会失败）'" v-model="showEdit"
             @close="showEdit = false">
    <el-button type="primary" @click="jsonDialogVisible = true">显示用户完整 JSON 数据</el-button>
    <el-button type="danger" @click="deleteMfaDevice" :loading="deleteMfaDeviceLoading">清除所有 MFA 设备</el-button>
    <el-button type="danger" @click="deleteApiKey" :loading="deleteApiKeyLoading">清除所有 API Key</el-button>
    <el-button type="warning" @click="resetPassword" :loading="resetPasswordLoading">重置密码</el-button>

    <el-form label-position="top" style="margin-top: 20px;">
      <el-form-item label="邮箱">
        <el-input v-model="UpdateUserParams.email" placeholder="邮箱" clearable></el-input>
      </el-form-item>
      <el-form-item label="dbUserName">
        <el-input v-model="UpdateUserParams.dbUserName" placeholder="dbUserName" clearable></el-input>
      </el-form-item>
      <el-form-item label="描述">
        <el-input v-model="UpdateUserParams.description" placeholder="描述" type="textarea"
                  rows="6" clearable></el-input>
      </el-form-item>
    </el-form>

    <div style="text-align: right; margin-top: 20px;">
      <el-button type="primary" @click="updateUserInfo" :loading="updateUserInfoLoading">修改</el-button>
    </div>

    <el-dialog title="JSON 数据" v-model="jsonDialogVisible" @close="jsonDialogVisible = false">
      <pre style="white-space: pre-wrap;">{{ jsonData }}</pre>
    </el-dialog>
  </el-dialog>

  <el-dialog
      v-model="updateCfgDialog"
      title="修改配置名称"
      width="30%"
      @close="updateCfgDialog = false"
  >
    <el-input v-model="updateCfgParams.updateCfgName" placeholder="请输入自定义配置名称，例：这是我的首尔" clearable/>
    <template #footer>
      <div class="dialog-footer">
        <el-button type="info" @click="updateCfgDialog = false">
          取消
        </el-button>
        <el-button type="primary" @click="updateOciCfgName()" :loading="updateCfgLoading">
          修改
        </el-button>
      </div>
    </template>
  </el-dialog>
</template>

<script>
import {ElMessage} from "element-plus";
import {confirmDialog} from '@/js/common.js'

export default {
  data() {
    return {
      updateCfgParams:{
        cfgId:'',
        updateCfgName: '',
      },
      updateCfgDialog: false,
      updateCfgLoading: false,
      loading: true,
      deleteMfaDeviceLoading: false,
      deleteApiKeyLoading: false,
      resetPasswordLoading: false,
      updateUserInfoLoading: false,
      showEdit: false,
      username: '',
      ociCfgId: '',
      total: 0,
      GetTenantUserListPageParams: {
        ociCfgId: '',
        region: '',
        cleanReLaunch: false
      },
      tenantInfo: {
        id: 'xxxxxxxxxxxx',
        name: 'yohann',
        description: 'xxxx',
        homeRegionKey: 'homeRegionKey',
        upiIdcsCompatibilityLayerEndpoint: 'upiIdcsCompatibilityLayerEndpoint',
        regions: ['tag1', 'tag2', 'tag3'],
        userList: []
      },
      UpdateUserId: '',
      UpdateUserName: '',
      jsonDialogVisible: false,
      jsonData: "",
      UpdateUserParams: {
        ociCfgId: '',
        userId: '',
        email: '',
        dbUserName: '',
        description: '',
      }
    };
  },
  mounted() {
    this.ociCfgId = this.$route.query.cfgId;
    this.username = this.$route.query.username;
    this.GetTenantUserListPageParams.ociCfgId = this.ociCfgId
    this.getTenantInfo()
  },
  methods: {
    reloadTenantInfo() {
      this.GetTenantUserListPageParams.cleanReLaunch = true
      this.loading = true
      this.getTenantInfo()
      this.GetTenantUserListPageParams.cleanReLaunch = false
    },
    async getTenantInfo() {
      this.loading = true
      this.GetTenantUserListPageParams.ociCfgId = this.ociCfgId
      await this.$axios.post('/tenant/tenantInfo', {...this.GetTenantUserListPageParams})
          .then(response => {
            if (response.data.success) {
              this.tenantInfo = response.data.data;
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
      this.loading = false
    },
    handleUpdateUsername(){
      this.updateCfgDialog = true
      this.updateCfgParams.cfgId = this.ociCfgId
      this.updateCfgParams.updateCfgName = this.username
    },
    async updateOciCfgName() {
      const confirmed = await confirmDialog('确定要修改配置名称吗？', '修改配置名称');
      if (!confirmed) {
        this.$message.info('已取消');
        return;
      }

      this.updateCfgLoading = true
      await this.$axios.post('/oci/updateCfgName', {...this.updateCfgParams})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: '修改配置名称成功',
                type: 'success',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
              this.username = this.updateCfgParams.updateCfgName
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
            this.updateCfgLoading = false
            this.updateCfgDialog = false
            this.reloadTenantInfo();
          })
          .catch(error => {
            console.error('Error:', error)
          })
    },
    handleEdit(row) {
      this.UpdateUserId = row.id
      this.UpdateUserName = row.name
      this.jsonData = row.jsonStr
      this.UpdateUserParams.email = row.email
      this.UpdateUserParams.dbUserName = row.email
      this.UpdateUserParams.description = row.description
      this.showEdit = true
    },
    async deleteUser(row) {
      const confirmed = window.confirm('确定要删除该用户吗？（🔔注意：可能会删除失败）');
      if (!confirmed) {
        this.$message.info('已取消');
        return
      }

      await this.$axios.post('/tenant/deleteUser', {
        ociCfgId: this.ociCfgId,
        userId: row.id,
      }).then(response => {
        if (response.data.success) {
          ElMessage({
            message: response.data.msg,
            type: 'success',
            duration: 2000 // 设置提示显示时间，单位是毫秒
          });
        } else {
          ElMessage({
            message: response.data.msg,
            type: 'error',
            duration: 2000 // 设置提示显示时间，单位是毫秒
          });
        }
      }).catch(error => {
        console.error('Error:', error)
      }).finally(() => {
        this.reloadTenantInfo()
      })
    },
    async deleteMfaDevice() {
      const confirmed = window.confirm('确定要清除所有 MFA 设备吗');
      if (!confirmed) {
        this.$message.info('已取消');
        return
      }

      this.deleteMfaDeviceLoading = true
      await this.$axios.post('/tenant/deleteMfaDevice', {
        ociCfgId: this.ociCfgId,
        userId: this.UpdateUserId,
      })
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
      this.deleteMfaDeviceLoading = false
    },
    async deleteApiKey() {
      const confirmed = window.confirm('确定要清除所有 API Key吗');
      if (!confirmed) {
        this.$message.info('已取消');
        return
      }

      this.deleteApiKeyLoading = true
      await this.$axios.post('/tenant/deleteApiKey', {
        ociCfgId: this.ociCfgId,
        userId: this.UpdateUserId,
      })
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
      this.deleteApiKeyLoading = false
    },
    async resetPassword() {
      const confirmed = window.confirm('确定要重置该用户的密码吗');
      if (!confirmed) {
        this.$message.info('已取消');
        return
      }

      this.resetPasswordLoading = true
      await this.$axios.post('/tenant/resetPassword', {
        ociCfgId: this.ociCfgId,
        userId: this.UpdateUserId,
      })
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
      this.resetPasswordLoading = false
    },
    async updateUserInfo() {
      const confirmed = window.confirm('确定要更新该用户信息吗');
      if (!confirmed) {
        this.$message.info('已取消');
        return
      }

      this.updateUserInfoLoading = true
      this.UpdateUserParams.ociCfgId = this.ociCfgId
      this.UpdateUserParams.userId = this.UpdateUserId
      await this.$axios.post('/tenant/updateUserInfo', {...this.UpdateUserParams})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
      this.updateUserInfoLoading = false
    },
  },
};
</script>

<style scoped>
.el-descriptions-item {
  display: flex; /* 使用 flexbox 布局 */
  align-items: center; /* 垂直居中 */
}

.el-descriptions-item__label {
  width: 200px; /* 设置标签宽度，根据需要调整 */
  text-align: left; /* 标签文本左对齐 */
  margin-right: 10px; /* 标签和内容之间的间距 */
}

.clearfix {
  margin-bottom: 10px;
}

.user-page {
  padding: 20px;
}

.user-info-card,
.user-list-card {
  margin-bottom: 20px;
}
</style>