<template>
  <el-tabs
      type="border-card"
      class="demo-tabs"
      v-model="activeTab"
  >
    <el-tab-pane label="OciTenant" name="OciTenant">
      <template #label>
        <span>
          <el-icon><Avatar/></el-icon>
          <span style="margin-left: 5px;">租户详情</span>
        </span>
      </template>
    </el-tab-pane>
    <el-tab-pane label="OciInstanceInfo" name="OciInstanceInfo">
      <template #label>
        <span>
          <el-icon><Cpu/></el-icon>
          <span style="margin-left: 5px;">实例详情</span>
        </span>
      </template>
    </el-tab-pane>
    <el-tab-pane label="BootVolumeList" name="BootVolumeList">
      <template #label>
        <span>
          <el-icon><Box/></el-icon>
          <span style="margin-left: 5px;">引导卷列表</span>
        </span>
      </template>
    </el-tab-pane>
    <el-tab-pane label="VcnList" name="VcnList">
      <template #label>
        <span>
          <el-icon><Platform/></el-icon>
          <span style="margin-left: 5px;">虚拟云网络</span>
        </span>
      </template>
    </el-tab-pane>
    <el-tab-pane label="Traffic" name="Traffic">
      <template #label>
        <span>
          <el-icon><DataAnalysis/></el-icon>
          <span style="margin-left: 5px;">流量统计</span>
        </span>
      </template>
    </el-tab-pane>
    <!-- 根据 activeTab 渲染内容 -->
    <div class="tab-content">
      <div v-if="activeTab === 'OciTenant'">
        <OciTenant :ociCfgId="cfgId" :username="username"/>
      </div>
      <div v-if="activeTab === 'OciInstanceInfo'">
        <OciInstanceInfo :cfg-id="cfgId" :username="username"/>
      </div>
      <div v-if="activeTab === 'BootVolumeList'">
        <BootVolumeList :cfg-id="cfgId" :username="username"/>
      </div>
      <div v-if="activeTab === 'VcnList'">
        <VcnList :cfg-id="cfgId" :username="username"/>
      </div>
      <div v-if="activeTab === 'Traffic'">
        <TrafficStatistics :ociCfgId="cfgId" :username="username"/>
      </div>
    </div>
  </el-tabs>
</template>

<script>
import OciInstanceInfo from '@/components/instance/OciInstanceInfo.vue';
import BootVolumeList from '@/components/volume/BootVolumeList.vue';
import VcnList from '@/components/vcn/VcnList.vue';
import TrafficStatistics from '@/components/ociuser/OciTrafficStatistics.vue';
import OciTenant from '@/components/ociuser/OciTenant.vue';

export default {
  components: {
    OciInstanceInfo, BootVolumeList, VcnList, TrafficStatistics,OciTenant
  },
  mounted() {
    // 从路由参数中初始化 cfgId 和 username
    this.cfgId = this.$route.query.cfgId;
    this.username = this.$route.query.username;
  },
  data() {
    return {
      activeTab: 'OciTenant', // 默认选中的标签
      cfgId: '', // 配置 ID
      username: '' // 用户名
    };
  },
  methods: {
    handleTabClick(tab) {
      console.log(`Switched to tab: ${tab.name}`);
      // 根据需要处理 tab 切换事件
      // 如果需要动态更新数据，可以在这里设置
    }
  },

};
</script>

