<template>
  <div style="display: flex; justify-content: flex-start; align-items: center; margin-bottom: 20px;">
    <!-- 左侧的按钮 -->
    <div>
      <el-button @click="goBack">返回配置列表</el-button>
      <el-button @click="reLaunchDetails" :loading="flushLoading" style="margin-left: 10px;">刷新缓存</el-button>
    </div>

    <!-- 右侧的标题 -->
    <span style="font-size: 16px; font-weight: bold; margin-left: 20px;">当前配置：{{ this.user.username }}</span>
  </div>


  <el-divider class="divider"/>

  <div class="instance-list">
    <div v-for="instance in ociCfgDetails.instanceList" :key="instance.ocId" class="instance-card">
      <div class="header">
        <el-icon class="server-icon">
          <cpu/>
        </el-icon>
        <h3>{{ instance.name }}</h3>
      </div>
      <el-divider class="divider"/>
      <div style="line-height: 1;">
        <p style="display: flex; align-items: center; margin: 10px;">
          <strong style="min-width: 100px;">Region</strong>
          <el-tag>{{ instance.region }}</el-tag>
        </p>
        <p style="display: flex; align-items: center; margin: 10px;">
          <strong style="min-width: 100px;">状态</strong>
          <el-tag :type="instance.state.indexOf('RUNNING') !== -1 ? 'success' : 'info'">
            {{ instance.state }}
          </el-tag>
        </p>
        <p style="display: flex; align-items: center; margin: 10px;">
          <strong style="min-width: 100px;">Shape</strong>
          <el-tag :type="instance.shape.indexOf('A1') !== -1 ? 'success' : 'info'">
            {{ instance.shape }}
          </el-tag>
        </p>
        <div style="display: flex; align-items: center; margin: 10px;">
          <strong style="min-width: 100px;">公网IP</strong>
          <div style="display: flex; flex-wrap: wrap; gap: 10px;">
            <el-tag
                v-for="(ip, ipIndex) in instance.publicIp"
                :key="ipIndex"
                effect="plain"
            >
              {{ ip }}
            </el-tag>
          </div>
        </div>
        <p style="display: flex; align-items: center; margin: 10px;">
          <strong style="min-width: 100px;">配置</strong>
          <el-tag effect="plain" type="info">
            {{ instance.ocpus }} 核 / {{ instance.memory }} GB / {{ instance.bootVolumeSize }} GB
          </el-tag>
        </p>
        <p style="display: flex; align-items: center; margin: 10px;">
          <strong style="min-width: 100px;">可用域</strong>
          <el-tag effect="plain" type="info">
            {{ instance.availabilityDomain }}
          </el-tag>
        </p>
        <p style="display: flex; align-items: center; margin: 10px;">
          <strong style="min-width: 100px;">创建时间</strong>
          <el-tag effect="plain" type="info">
            {{ instance.createTime }}
          </el-tag>
        </p>
      </div>
      <el-divider class="divider"/>
      <el-button-group class="ml-4">
        <el-tooltip content="更换IP" placement="bottom">
          <el-button @click="handleChangeIp(instance.ocId)" icon="HelpFilled"
                     v-if="instance.enableChangeIp !== 1">
          </el-button>
        </el-tooltip>

        <el-tooltip content="停止更换IP" placement="bottom">
          <el-button @click="handleStopChangeIp(instance.ocId)" icon="HelpFilled"
                     v-if="instance.enableChangeIp === 1"
                     type="danger">
          </el-button>
        </el-tooltip>

        <el-tooltip content="编辑实例配置" placement="bottom">
          <el-button @click="handleUpdateInstanceCfg(instance.ocId)" icon="Edit"
                     type="primary">
          </el-button>
        </el-tooltip>

        <el-tooltip content="启动实例" placement="bottom">
          <el-button type="success" icon="VideoPlay"
                     :disabled="instance.state.indexOf('RUNNING') !== -1"
                     v-if="instance.state.indexOf('RUNNING') === -1"
                     @click="updateInstanceState(instance.ocId, 'START')"/>
        </el-tooltip>

        <el-tooltip content="停止实例" placement="bottom">
          <el-button type="info" icon="VideoPause"
                     :disabled="instance.state.indexOf('STOPPED') !== -1"
                     v-if="instance.state.indexOf('STOPPED') === -1"
                     @click="updateInstanceState(instance.ocId, 'STOP')"/>
        </el-tooltip>

        <el-tooltip content="重启实例" placement="bottom">
          <el-button type="warning" icon="Refresh"
                     @click="updateInstanceState(instance.ocId, 'RESET')"/>
        </el-tooltip>

        <el-tooltip content="终止实例（操作不可逆，请谨慎操作）" placement="bottom">
          <el-button type="danger" icon="Delete" :disabled="instance.state.indexOf('TERMINATING') !== -1"
                     @click="handleTerminateInstance(instance.ocId)"/>
        </el-tooltip>

        <el-tooltip content="启动 Cloud Shell 连接" placement="bottom">
          <el-button type="success" icon="HelpFilled"
                     @click="startVnc(instance.ocId)"/>
        </el-tooltip>
      </el-button-group>
    </div>
  </div>

  <div class="demo-collapse">
    <el-collapse accordion>
      <el-collapse-item name="1">
        <template #title>
          <el-icon class="header-icon">
            <info-filled/>
          </el-icon>
          API配置详情（点击向下⬇展开）
        </template>
        <div><span style="margin-left: 20px;"><el-text type="info">region={{
            this.ociCfgDetails.region
          }}</el-text></span></div>
        <div><span style="margin-left: 20px;"><el-text type="info">user={{ this.ociCfgDetails.userId }}</el-text></span>
        </div>
        <div><span style="margin-left: 20px;"><el-text type="info">tenancy={{
            this.ociCfgDetails.tenantId
          }}</el-text></span></div>
        <div><span style="margin-left: 20px;"><el-text type="info">fingerprint={{
            this.ociCfgDetails.fingerprint
          }}</el-text></span></div>
        <div><span style="margin-left: 20px;"><el-text type="info">key_file={{
            this.ociCfgDetails.privateKeyPath
          }}</el-text></span></div>
      </el-collapse-item>
    </el-collapse>
  </div>

  <el-dialog v-model="dialogVisible" title="可指定多个 CIDR 网段，回车保存，传空则随机更换一个IP" width="50%"
             @close="handleDialogClose">

    <!-- 表单区域整体开始 -->
    <el-form :model="changeIpParams" label-width="100px" class="form-section">

      <!-- vnic选择 -->
      <el-form-item label="选择VNIC">
        <el-select v-model="changeIpParams.vnicId" placeholder="请选择vnic" style="width: 100%;">
          <el-option
              v-for="item in vnicList"
              :key="item.vnicId"
              :label="item.name"
              :value="item.vnicId"
          />
        </el-select>
      </el-form-item>

      <!-- CIDR管理 -->
      <el-form-item label="CIDR列表">
        <div style="display: flex; flex-wrap: wrap; gap: 4px;">
          <el-tag
              v-for="tag in changeIpParams.cidrList"
              :key="tag"
              closable
              @close="handleClose(tag)"
          >
            {{ tag }}
          </el-tag>
        </div>
        <div style="margin-top: 8px;">
          <el-input
              v-if="inputVisible"
              ref="InputRef"
              v-model="inputValue"
              class="w-20"
              @keyup.enter="handleInputConfirm"
              @blur="handleInputConfirm"
              placeholder="回车添加CIDR"
          />
          <el-button v-else class="button-new-tag" size="small" @click="showInput">
            + 新增 CIDR 网段
          </el-button>
        </div>
      </el-form-item>

      <!-- 分隔线 -->
      <el-form-item>
        <el-divider class="divider"/>
      </el-form-item>

      <!-- 高级设置控制按钮 -->
      <el-form-item>
        <el-button type="text" @click="advancedVisible = !advancedVisible">
          {{ advancedVisible ? '收起 Cloudflare DNS 设置' : '点击展开 Cloudflare DNS 设置' }}
        </el-button>
      </el-form-item>

      <!-- 高级设置区域 -->
      <template v-if="advancedVisible">
        <el-form-item label="域名前缀">
          <el-input v-model="changeIpParams.domainPrefix" placeholder="输入前缀"/>
        </el-form-item>

        <el-form-item label="选择域名">
          <el-select v-model="changeIpParams.selectedDomainCfgId" placeholder="请选择域名" style="width: 100%;">
            <el-option
                v-for="item in ociCfgDetails.cfCfgList"
                :key="item.cfCfgId"
                :label="item.domain"
                :value="item.cfCfgId"
            />
          </el-select>
        </el-form-item>

        <el-form-item label="开启代理加速">
          <el-switch v-model="changeIpParams.enableProxy"/>
        </el-form-item>

        <el-form-item label="TTL（秒）">
          <el-input-number v-model="changeIpParams.ttl" :min="60" :max="86400" :step="60" placeholder="单位：秒"/>
        </el-form-item>

        <el-form-item label="备注">
          <el-input type="textarea" v-model="changeIpParams.remark" placeholder="输入备注信息"/>
        </el-form-item>
      </template>

    </el-form>
    <!-- 表单区域整体结束 -->

    <!-- 弹窗底部按钮 -->
    <template #footer>
      <el-button @click="handleDialogClose">取消</el-button>
      <el-button type="primary" @click="changeIp" :loading="changeIpLoading">
        新增更换IP任务
      </el-button>
    </template>

  </el-dialog>


  <!-- 弹框 -->
  <el-dialog
      title="终止实例"
      v-model="terminateDialogVisible"
      width="500px"
      @close="terminateDialogVisible = false"
  >
    <div>
      <p>终止实例将不可恢复，请确认以下选项：</p>
      <el-row>
        <!-- 选择是否保留引导卷及数据 -->
        <el-col :span="24">
          <el-checkbox v-model="retainBootVolume">
            保留引导卷及数据
          </el-checkbox>
        </el-col>
      </el-row>
      <el-divider class="divider"/>
      <el-row>
        <!-- 验证码输入框 -->
        <el-col :span="24">
          <el-form :model="form" ref="form" label-width="70px">
            <el-form-item label="验证码">
              <el-input
                  v-model="form.verificationCode"
                  placeholder="请输入验证码"
                  maxlength="10"
                  clearable
              ></el-input>
            </el-form-item>
          </el-form>
        </el-col>
      </el-row>
    </div>

    <!-- 弹框操作按钮 -->
    <template #footer>
      <el-button @click="terminateDialogVisible = false">取消</el-button>
      <el-button @click="sendCaptcha" :loading="sendLoading">发送验证码</el-button>
      <el-button
          type="danger"
          :disabled="form.verificationCode === ''"
          :loading="terminateLoading"
          @click="terminateInstance"
      >
        确认终止
      </el-button>
    </template>
  </el-dialog>
</template>

<script>
import {ElMessage, ElMessageBox, ElLoading} from "element-plus";
import {cpu} from '@element-plus/icons-vue';
import {confirmDialog} from "@/js/common";

export default {
  components: {cpu},
  data() {
    return {
      advancedVisible: false,
      flushLoading: false,
      changeIpLoading: false,
      sendLoading: false,
      dialogVisible: false,
      terminateDialogVisible: false,
      terminateLoading: false, // 控制确认按钮加载状态
      retainBootVolume: false, // 是否保留引导卷
      form: {
        verificationCode: "", // 输入的验证码
      },
      inputVisible: false,
      inputValue: '',
      user: {},
      changeIpParams: {
        ociCfgId: '',
        instanceId: '',
        cidrList: [],
        changeCfDns: false,
        vnicId: '',
        domainPrefix: '',
        selectedDomainCfgId: '',
        enableProxy: false,
        ttl: 60,
        remark: ''
      },
      vnicList: [],
      ociCfgDetails: {},
      UpdateInstanceStateParams: {
        ociCfgId: '',
        instanceId: '',
        action: ''
      },
      SendCaptchaParams: {
        ociCfgId: '',
        instanceId: ''
      },
      TerminateInstanceParams: {
        ociCfgId: '',
        instanceId: '',
        preserveBootVolume: 0,
        captcha: ''
      },
      GetOciCfgDetailsParams: {
        cfgId: '',
        cleanReLaunchDetails: false
      },
      compartmentId: ''
    };
  },
  mounted() {
    this.user.id = this.$route.query.cfgId
    this.user.username = this.$route.query.username
    this.changeIpParams.ociCfgId = this.$route.query.cfgId
    this.GetOciCfgDetailsParams.cfgId = this.$route.query.cfgId
    this.getOciCfgDetails()
  },
  methods: {
    goBack() {
      this.$router.back(); // 返回上一个页面
    },
    handleClose(tag) {
      const index = this.changeIpParams.cidrList.indexOf(tag);
      if (index !== -1) {
        this.changeIpParams.cidrList.splice(index, 1);
      }
    },
    handleDialogClose() {
      this.dialogVisible = false;
      this.changeIpParams.vnicId = ''
      this.changeIpParams.cidrList = []
    },
    handleStopChangeIp(ocId) {
      const confirmed = window.confirm('确定要停止更换IP任务吗?');
      if (confirmed) {
        this.stopChangeIp(ocId);
      } else {
        this.$message.info('已取消');
      }
    },
    handleUpdateInstanceCfg(ocId) {
      this.$router.push({
        path: '/dashboard/ociUpdateInstanceCfg',
        query: {ocId: ocId, ociCfgId: this.user.id, username: this.user.username}
      });
    },
    stopChangeIp(ocId) {
      this.$axios.post('/oci/stopChangeIp', {instanceId: ocId})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
            this.getOciCfgDetails()
          })
          .catch(error => {
            console.error('Error:', error)
          })
    },
    showInput() {
      this.inputVisible = true;
      this.$nextTick(() => {
        this.$refs.InputRef.focus();
      });
    },
    handleInputConfirm() {
      if (this.inputValue && !this.changeIpParams.cidrList.includes(this.inputValue)) {
        this.changeIpParams.cidrList.push(this.inputValue);
      }
      this.inputValue = '';
      this.inputVisible = false;
    },
    reLaunchDetails() {
      this.GetOciCfgDetailsParams.cleanReLaunchDetails = true
      this.getOciCfgDetails()
      this.GetOciCfgDetailsParams.cleanReLaunchDetails = false
    },
    async getOciCfgDetails() {
      this.ociCfgDetails.instanceList = []
      this.flushLoading = true
      await this.$axios.post('/oci/details', {
        cfgId: this.changeIpParams.ociCfgId,
        cleanReLaunchDetails: this.GetOciCfgDetailsParams.cleanReLaunchDetails
      })
          .then(response => {
            if (response.data.success) {
              this.ociCfgDetails = response.data.data
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
              this.flushLoading = false
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
    },
    handleChangeIp(id) {
      this.dialogVisible = true
      this.changeIpParams.instanceId = id
      this.vnicList = this.ociCfgDetails.instanceList.find(item => item.ocId === id)?.vnicList
    },
    changeIp() {
      this.changeIpLoading = true
      this.changeIpParams.changeCfDns = this.advancedVisible
      this.$axios.post('/oci/changeIp', {...this.changeIpParams})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
              this.changeIpLoading = false
              this.dialogVisible = false
              this.getOciCfgDetails()
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
          .finally(() => {
            this.changeIpLoading = false
          })
    },
    async updateInstanceState(id, action) {
      // 根据 action 设置提示内容
      let actionMessage = '';
      switch (action) {
        case 'START':
          actionMessage = '确定要启动实例吗？';
          break;
        case 'STOP':
          actionMessage = '确定要停止实例吗？';
          break;
        case 'RESET':
          actionMessage = '确定要重新启动实例吗？';
          break;
        default:
          actionMessage = '确定要执行此操作吗？';
      }
      try {
        // 弹框确认
        const confirmResult = await ElMessageBox.confirm(
            actionMessage, // 弹框内容
            '确认操作', // 弹框标题
            {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning', // 弹框类型
            }
        );

        // 如果用户点击了 "确定"
        if (confirmResult === 'confirm') {
          this.UpdateInstanceStateParams.ociCfgId = this.user.id
          this.UpdateInstanceStateParams.instanceId = id
          this.UpdateInstanceStateParams.action = action

          await this.$axios
              .post('/oci/updateInstanceState', {...this.UpdateInstanceStateParams})
              .then((response) => {
                if (response.data.success) {
                  ElMessage({
                    message: response.data.msg,
                    type: 'success',
                    duration: 2000, // 设置提示显示时间，单位是毫秒
                  });
                  this.getOciCfgDetails();
                } else {
                  ElMessage({
                    message: response.data.msg,
                    type: 'error',
                    duration: 2000,
                  });
                }
              })
              .catch((error) => {
                console.error('Error:', error);
                ElMessage({
                  message: '更新实例状态时出错',
                  type: 'error',
                  duration: 2000,
                });
              });
        }
      } catch (cancelError) {
        // 用户点击了 "取消" 或关闭了弹框，不需要执行任何操作
        ElMessage({
          message: '操作已取消',
          type: 'info',
          duration: 2000,
        });
      }
    },
    handleTerminateInstance(id) {
      this.TerminateInstanceParams.instanceId = id
      this.terminateDialogVisible = true
    },
    sendCaptcha() {
      this.sendLoading = true
      this.SendCaptchaParams.ociCfgId = this.user.id
      this.SendCaptchaParams.instanceId = this.TerminateInstanceParams.instanceId
      this.$axios.post('/oci/sendCaptcha', {...this.SendCaptchaParams})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
              this.changeIpLoading = false
              this.dialogVisible = false
              this.sendLoading = false
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
    },
    async terminateInstance() {
      this.terminateLoading = true;

      this.TerminateInstanceParams.ociCfgId = this.user.id
      this.TerminateInstanceParams.preserveBootVolume = this.retainBootVolume === true ? 1 : 0
      this.TerminateInstanceParams.captcha = this.form.verificationCode
      // 发送请求
      this.$axios.post('/oci/terminateInstance', {...this.TerminateInstanceParams})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
              this.getOciCfgDetails()
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
            ElMessage({
              message: "终止实例时发生错误",
              type: "error",
              duration: 2000,
            });
          })
          .finally(() => {
            this.terminateLoading = false;
            this.terminateDialogVisible = false; // 关闭弹框
          })
    },

    async startVnc(instanceId) {
      const confirmed = await confirmDialog('确定要连接到该实例吗？', 'Cloud Shell');
      if (!confirmed) {
        this.$message.info('已取消');
        return;
      }

      // 弹出加载框
      const loadingInstance = ElLoading.service({
        lock: true,
        text: '正在启动 VNC 连接，请稍候...',
        background: 'rgba(0, 0, 0, 0.5)'
      });

      this.$axios.post('/oci/startVnc', {
        ociCfgId: this.user.id,
        instanceId: instanceId,
        compartmentId: this.compartmentId
      })
          .then(response => {
            if (response.data.success) {
              loadingInstance.close(); // ✅ 关闭加载框

              let baseUrl;
              if (location.protocol === 'http:') {
                const hostWithoutPort = location.hostname;
                baseUrl = `http://${hostWithoutPort}:6080`;
              } else {
                baseUrl = `${location.protocol}//${location.host}/myvnc`;
              }

              const targetUrl = `${baseUrl}/vnc.html?autoconnect=true`;

              ElMessageBox({
                title: '🎉 VNC 连接成功 🎉',
                message: `${response.data.msg} VNC 连接创建成功！页面即将自动跳转~<br><br>连接地址：${targetUrl}<br><br>❗如被浏览器拦截，请复制连接地址进行访问，如果没有配置反向代理，需要放行部署探长的服务器的 6080 端口。`,
                dangerouslyUseHTMLString: true,
                confirmButtonText: '知道了',
                showClose: true,
              });

              setTimeout(() => {
                window.open(targetUrl, '_blank');
              }, 4000);

            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000
              });
              loadingInstance.close();
            }
          })
          .catch(() => {
            ElMessage({
              message: "连接时发生错误",
              type: "error",
              duration: 2000,
            });
            loadingInstance.close(); // ✅ 关闭加载框
          })
    }
  }

};
</script>

<style>
.instance-list {
  display: flex;
  flex-wrap: wrap;
  text-align: right;
}

.instance-card {
  border: 1px solid #ddd;
  padding: 16px;
  margin: 10px;
  width: 100%;
  max-width: 300px;
  border-radius: 8px;
//background-color: #f9f9f9; position: relative; box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); text-align: left; /* Adjust text alignment */
}

.header {
  display: flex;
  align-items: center;
}

.server-icon {
  font-size: 24px;
  color: #409eff;
  margin-right: 10px; /* Add margin to separate the icon and text */
}

.ip-item {
  display: flex;
  align-items: center;
