<template>
  <span style="font-size: 16px; font-weight: bold;">当前配置：{{ this.username }}</span>
  <el-divider class="divider"/>
  <div>
    <el-button type="success"
               style="margin-bottom: 20px;"
               @click="cleanReLaunch">
      刷新
    </el-button>
    <el-input
        v-model="pageParams.keyword"
        placeholder="模糊搜索"
        @input="getBvList"
        clearable prefix-icon="Search"
        style="margin-left: 20px; width: 200px; margin-bottom: 20px;"
    />
    <el-button
        type="danger"
        @click="batchTerminate"
        :disabled="!batchSelected.length"
        style="margin-left: 20px; margin-bottom: 20px;"
    >
      批量终止
    </el-button>
    <!-- 可滑动的表格 -->
    <div style="overflow-x: auto;">
      <el-table
          :data="BootVolumesRsp"
          style="width: 100%; min-width: 800px;"
          v-loading="loading"
          @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="35" :selectable="isSelectable"/>
        <el-table-column fixed label="名称" width="300">
          <template #default="{ row }">
            <el-tooltip :content="row.displayName" placement="top">
              {{ row.displayName }}
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column label="可用域">
          <template #default="{ row }">
            <el-tooltip :content="row.availabilityDomain" placement="top">
              <el-tag type="info">
                {{ row.availabilityDomain }}
              </el-tag>
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column label="状态">
          <template #default="{ row }">
            <el-tag :type="row.lifecycleState === 'AVAILABLE' ? 'success' : 'info'">
              {{ row.lifecycleState }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column label="已附加实例">
          <template #default="{ row }">
            <el-tooltip :content="row.instanceName" placement="top">
              <el-tag type="primary" v-if="row.instanceName !== null">
                {{ row.instanceName }}
              </el-tag>
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column prop="vpusPerGB" label="VPU"/>
        <el-table-column prop="sizeInGBs" label="硬盘（GB）"/>
        <el-table-column prop="timeCreated" label="创建时间"/>

        <el-table-column fixed="right" label="操作" width="150">
          <template #default="{ row }">
            <el-button @click="handleUpdateBootVolume(row)" type="primary" link>
              编辑
            </el-button>
            <el-button @click="handleTerminate(row)" type="danger" link
                       :disabled="row.attached">
              终止
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!-- 分页控制 -->
    <el-pagination
        @current-change="handlePageChange"
        @size-change="handleSizeChange"
        v-model:current-page="pageParams.currentPage"
        v-model:page-size="pageParams.pageSize"
        :page-sizes="[5, 10, 20, 50]"
        :total="total"
        layout="total, sizes, prev, pager, next, jumper"
        style="margin-top: 20px;"
    />
  </div>

  <el-dialog
      title="更改引导卷配置"
      v-model="updateBvDialogVisible"
      width="500px"
      @close="updateBvDialogVisible = false"
  >
    <el-row :gutter="20" align="middle" style="margin-top: 20px">
      <!-- 引导卷大小 -->
      <el-col :span="24" style="margin-bottom: 10px;">
        <span style="font-size: 14px; font-weight: bold;">引导卷磁盘大小（GB）：</span>
        <el-input-number
            v-model="UpdateBootVolumeParams.bootVolumeSize"
            :min="47"
            label="引导卷大小"
            style="margin-left: 10px;"
        ></el-input-number>
      </el-col>

      <!-- 引导卷 VPU -->
      <el-col :span="24">
        <span style="font-size: 14px; font-weight: bold;">引导卷 VPU：</span>
        <el-slider
            v-model="UpdateBootVolumeParams.bootVolumeVpu"
            :min="10"
            :max="120"
            :step="10"
            label="引导卷VPU大小"
            style="margin-left: 10px; max-width: 70%;"
        ></el-slider>
        <span style="margin-left: 10px;">当前 VPU: {{ selectedBvVpu }}</span>
      </el-col>
    </el-row>

    <!-- 操作按钮 -->
    <template #footer>
      <el-row type="flex" justify="end" style="margin-top: 20px;">
        <el-button @click="updateBvDialogVisible = false">取消</el-button>
        <el-button
            type="primary"
            :loading="updateBvLoading"
            @click="updateBootVolume"
        >
          确认修改
        </el-button>
      </el-row>
    </template>
  </el-dialog>

</template>

<script>
import {ElMessage} from "element-plus";

export default {
  mounted() {
    this.pageParams.ociCfgId = this.$route.query.cfgId
    this.TerminateBootVolumeParams.ociCfgId = this.$route.query.cfgId
    this.UpdateBootVolumeParams.ociCfgId = this.$route.query.cfgId
    this.username = this.$route.query.username
    this.getBvList()
  },
  data() {
    return {
      username: '', // 用户名
      pageParams: {
        cleanReLaunch: false,
        ociCfgId: '',
        keyword: '',
        currentPage: 1,
        pageSize: 10,
      },
      total: 0,
      BootVolumesRsp: [],
      batchSelected: [],
      TerminateBootVolumeParams: {
        ociCfgId: '',
        bootVolumeIds: []
      },
      loading: true,
      updateBvDialogVisible: false,
      updateBvLoading: false,
      selectedBvVpu: '',
      UpdateBootVolumeParams: {
        ociCfgId: '',
        bootVolumeId: '',
        bootVolumeSize: '',
        bootVolumeVpu: '',
      }
    };
  },
  methods: {
    async getBvList() {
      await this.$axios.post('/bootVolume/page', {...this.pageParams})
          .then(response => {
            if (response.data.success) {
              // ElMessage({
              //   message: response.data.msg,
              //   type: 'success',
              //   duration: 2000 // 设置提示显示时间，单位是毫秒
              // });
              this.BootVolumesRsp = response.data.data.records;
              this.total = response.data.data.total;
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
            this.loading = false
          })
          .catch(error => {
            console.error('Error:', error)
          })
    },
    cleanReLaunch() {
      this.pageParams.cleanReLaunch = true
      this.loading = true
      this.getBvList()
      this.pageParams.cleanReLaunch = false
    },
    async batchTerminate() {
      const confirmed = window.confirm('确定要终止引导卷吗');
      if (!confirmed) {
        this.$message.info('已取消');
        return
      }
      this.TerminateBootVolumeParams.bootVolumeIds = this.batchSelected.map(record => record.id);
      await this.$axios.post('/bootVolume/terminate', {...this.TerminateBootVolumeParams})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
              this.cleanReLaunch()
              this.batchSelected = []
              this.TerminateBootVolumeParams.bootVolumeIds = []
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
    },
    handleTerminate(row) {
        this.batchSelected.push(row)
        this.batchTerminate()
    },
    handleUpdateBootVolume(row) {
      this.updateBvDialogVisible = true
      this.UpdateBootVolumeParams.bootVolumeId = row.id
      this.UpdateBootVolumeParams.bootVolumeSize = row.sizeInGBs
      this.selectedBvVpu = row.vpusPerGB
    },
    async updateBootVolume() {
      const confirmed = window.confirm('确定要更改引导卷配置吗');
      if (!confirmed) {
        this.$message.info('已取消');
        this.updateBvDialogVisible = false
        return
      }
      this.updateBvLoading = true
      await this.$axios.post('/bootVolume/update', {...this.UpdateBootVolumeParams})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
              this.cleanReLaunch()
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
          }).catch(error => {
            console.error('Error:', error)
          }).finally(() => {
            this.updateBvLoading = false
            this.updateBvDialogVisible = false
          })
    },
    isSelectable(row) {
      // 示例：禁止选中 id 为 1 的行
      return !row.attached;
    },
    handlePageChange(page) {
      this.pageParams.currentPage = page;
      this.getBvList();
    },
    handleSizeChange(size) {
      this.pageParams.currentPage = 1;
      this.pageParams.pageSize = size;
      this.getBvList();
    },
    handleSelectionChange(val) {
      this.batchSelected = val;
    },
  }
}
</script>

